<template>
    <div class="loginBox">
        <el-form class="fromBox" ref="ruleForm" :model="ruleForm" label-position="top" :rules="rules">
            <div class="login-title">昆仑能源</div>
            <div class="errMsg">{{msg}}</div>
            <el-form-item prop="userName" label="账号">
                <el-input v-model="ruleForm.userName" @keyup.enter.native="submitForm" placeholder="请输入账号"></el-input>
            </el-form-item>
            <el-form-item prop="password" label="密码">
                <el-input v-model="ruleForm.password" @keyup.enter.native="submitForm" type="password"
                    placeholder="请输入密码" auto-complete="off">
                </el-input>
            </el-form-item>
            <el-form-item prop="code" label="验证码" class="code-box">
                <el-input v-model="ruleForm.code" @keyup.enter.native="submitForm" placeholder="请输入验证码"
                    auto-complete="off"></el-input>
                <img class="code-img" @click="getCode" :src="codeUrl" alt="" />
            </el-form-item>
            <div class="rember-box">
                <el-checkbox v-model="ruleForm.rememberMe">记住密码</el-checkbox>
            </div>
            <div class="sumbtn-box">
                <!-- <div @click="rigist()">注册</div> -->
                <div @click="submitForm">登录</div>
            </div>
        </el-form>
        <div class="version-label" :title="desc">v{{version}}</div>
    </div>
</template>

<script>
    import {
        getImage,
        login,
        getVersion,
    } from '@/api/login'
    import tool from '@/utils/tool'
    import Cookies from 'js-cookie'
    import {
        encrypt, //加密
        decrypt //解密
    } from '@/utils/jsencrypt'
    export default {
        name: 'login',
        data() {
            return {
                loading: false,
                remberPass: false,
                codeUrl: '',
                uuid: '',
                ruleForm: {
                    userName: '',
                    password: '',
                    rememberMe: false,
                    code: '',
                    uuid: ''
                },
                rules: {
                    userName: [{
                        required: true,
                        trigger: 'blur',
                        message: '用户名不能为空'
                    }],
                    password: [{
                        required: true,
                        trigger: 'blur',
                        message: '密码不能为空'
                    }],
                    code: [{
                        required: true,
                        trigger: 'change',
                        message: '验证码不能为空'
                    }]
                },
                msg: '',
                version: '', //版本号
                desc: '', //版本描述
            }
        },
        created() {
            this.getCode();
            this.getCookie();
            //获取版本号
            getVersion().then(({
                data
            }) => {
                this.version = data.version;
                this.desc = '【版本说明】' + data.desc;
            })
        },
        methods: {
            getCode() {
                getImage().then(res => {
                    this.codeUrl = 'data:image/gif;base64,' + res.img
                    this.ruleForm.uuid = res.uuid
                })
            },
            getCookie() {
                const userName = Cookies.get('userName')
                const password = Cookies.get('password')
                const rememberMe = Cookies.get('rememberMe')

                this.ruleForm = {
                    userName: userName === undefined ? this.ruleForm.userName : userName,
                    password: password === undefined ? this.ruleForm.password : decrypt(password),
                    rememberMe: rememberMe === undefined ? false : Boolean(rememberMe)
                }
            },
            submitForm() {
                if (this.loading) return
                this.$refs.ruleForm.validate((valid) => {
                    if (valid) {
                        //发送请求
                        this.loading = true
                        if (this.ruleForm.rememberMe) {
                            Cookies.set('userName', this.ruleForm.userName, {
                                expires: 30
                            })
                            Cookies.set('password', encrypt(this.ruleForm.password), {
                                expires: 30
                            })
                            Cookies.set('rememberMe', this.ruleForm.rememberMe, {
                                expires: 30
                            })
                        } else {
                            Cookies.remove('userName')
                            Cookies.remove('password')
                            Cookies.remove('rememberMe')
                        }

                        this.$store.dispatch('Login', this.ruleForm).then((res) => {
                            this.$router.push("/");
                        }).catch((res) => {
                            this.loading = false;
                            this.msg = res.msg
                            this.getCode()
                        })
                    } else {
                        return false;
                    }
                });
            },
            rigist() {}
        }
    }
</script>
<style lang="scss">
    .fromBox {
        .el-input input {
            background: none;
            border: none;
            border-radius: 0;
            font-size: 16px;
            border-bottom: 1px solid #bcc8cc;
            color: #0c2d3b;
            padding: 0 5px;
        }
    }
</style>
<style scoped lang="scss">
    .errMsg {
        color: red;
    }

    .loginBox {
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        background: url("https://cdn.jsdelivr.net/npm/dldf-cdn@1.0.1/dist/bgsmall.png") no-repeat;
        background-size: cover;
        -webkit-background-size: cover;
        -o-background-size: cover;
        background-position: center center;
    }

    .login-title {
        color: #fff;
        letter-spacing: 10px;
        font-size: 48px;
        margin-bottom: 70px;
        margin-left: -8px;
    }

    .fromBox {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        right: 126px;
        width: 360px;
        height: 500px;
        color: #fff;

        ::v-deep .el-input__inner {
            background: none;
            border: none;
            color: #fff;
            padding: 0 0px;
            border-bottom: 1px solid rgba(255, 255, 255, 0.28);
        }

        ::v-deep .el-input__inner ::placeholder {
            color: rgba(255, 255, 255, 0.4);
        }
    }

    .code-box {
        position: relative;
    }

    .code-img {
        position: absolute;
        cursor: pointer;
        right: 0;
        width: 108px;
        height: 44px;
        border-radius: 2px;
        border: 1px solid #4e555e;
        bottom: -1px;
    }

    .rember-box {
        ::v-deep.el-checkbox__label {
            color: rgba(255, 255, 255, 0.4);
        }
    }

    .sumbtn-box {
        margin-top: 50px;
        display: flex;
        justify-content: flex-end;

        div {
            width: 100px;
            height: 35px;
            text-align: center;
            line-height: 35px;
            border-radius: 4px;
            cursor: pointer;
        }

        // div:nth-child(1) {
        //   background: #fff;
        //   color: #333;
        //   margin-right: 30px;
        // }
        div:nth-child(1) {
            background: #2e4af7;
            color: #fff;

        }


    }

    .version-label {
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        text-align: center;
        color: #fff;
        font-size: 12px;
    }
</style>